"use client";

import React, { createContext, useContext, useState, ReactNode } from 'react';
import { CheckCircle, XCircle, AlertTriangle, Info, X, Sparkles } from 'lucide-react';

interface Toast {
  id: string;
  message: string;
  type: 'success' | 'error' | 'info' | 'warning' | 'sage';
  duration?: number;
}

interface ToastContextType {
  showToast: (message: string, type?: Toast['type'], duration?: number) => void;
  removeToast: (id: string) => void;
}

const ToastContext = createContext<ToastContextType | undefined>(undefined);

interface ToastProviderProps {
  children: ReactNode;
}

export function ToastProvider({ children }: ToastProviderProps) {
  const [toasts, setToasts] = useState<Toast[]>([]);

  // Generate unique ID to prevent collisions
  const generateUniqueId = () => {
    return `toast_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
  };

  const showToast = (message: string, type: Toast['type'] = 'info', duration = 4000) => {
    const id = generateUniqueId();
    const newToast: Toast = { id, message, type, duration };

    setToasts(prev => [...prev, newToast]);

    // Auto remove toast after duration
    setTimeout(() => {
      removeToast(id);
    }, duration);
  };

  const removeToast = (id: string) => {
    setToasts(prev => prev.filter(toast => toast.id !== id));
  };

  const getToastStyles = (type: Toast['type']) => {
    switch (type) {
      case 'success':
        return 'bg-white   text-sage';
      case 'sage':
        return 'bg-white   text-sage';
      case 'error':
        return 'bg-white   text-red-500';
      case 'warning':
        return 'bg-white   text-amber-500';
      default:
        return 'bg-white   text-charcoal';
    }
  };

  const getToastIcon = (type: Toast['type']) => {
    const iconClasses = "w-5 h-5 flex-shrink-0";
    switch (type) {
      case 'success':
        return <CheckCircle className={iconClasses} />;
      case 'sage':
        return <Sparkles className={iconClasses} />;
      case 'error':
        return <XCircle className={iconClasses} />;
      case 'warning':
        return <AlertTriangle className={iconClasses} />;
      default:
        return <Info className={iconClasses} />;
    }
  };

  const value: ToastContextType = {
    showToast,
    removeToast
  };

  return (
    <ToastContext.Provider value={value}>
      {children}

      {/* Toast Container - Bottom-left position */}
      {toasts.length > 0 && (
        <div className="fixed bottom-4 left-4 z-[9999] flex flex-col-reverse gap-2 pointer-events-none max-w-sm">
          {toasts.map((toast) => (
            <div
              key={toast.id}
              className={`
                pointer-events-auto max-w-sm w-full backdrop-blur-xl rounded-xl p-4 shadow-2xl border border-sage/20
                transition-all duration-300 ease-out animate-in slide-in-from-left
                ${getToastStyles(toast.type)}
              `}
            >
              <div className="flex items-center gap-3">
                <div className="flex-shrink-0">
                  {getToastIcon(toast.type)}
                </div>
                <div className="flex-1 min-w-0">
                  <p className="font-sf text-sm font-600 leading-tight">
                    {toast.message}
                  </p>
                </div>
                <button
                  onClick={() => removeToast(toast.id)}
                  className="flex-shrink-0 ml-2 opacity-70 hover:opacity-100 transition-opacity duration-200 p-1 hover:bg-charcoal/10 rounded-full"
                  aria-label="Dismiss notification"
                >
                  <X className="w-4 h-4" />
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </ToastContext.Provider>
  );
}

export function useToast() {
  const context = useContext(ToastContext);
  if (context === undefined) {
    throw new Error('useToast must be used within a ToastProvider');
  }
  return context;
}